<template>
  <v-app>
    <v-container fluid>
      <v-row no-gutters>
        <!-- 左半部 -->
        <v-col cols="4" class="main_part d-none d-md-flex d-lg-flex">
          <!-- <p>CIMI管理員後台系統</p> -->
          <div class="pic">
            <img src="@/assets/login_logo.svg" alt="" />
          </div>
          <img src="@/assets/blue_ball.png" alt="" class="blue_ball" />
        </v-col>

        <!-- 右半部 -->
        <v-col
          cols="12"
          sm="12"
          md="8"
          lg="8"
          class="login_part d-flex justify-center align-center"
        >
          <div class="pic">
            <img src="@/assets/pink_ball.png" alt="" />
          </div>

          <v-col cols="10" lg="6" class="form">
            <v-form ref="form" v-model="valid" lazy-validation>
              <h3 class="mb-12">CIMI ADMIN SYSTEM</h3>

              <v-text-field
                v-model="email"
                :rules="nameRules"
                :error-messages="nameErrors"
                label="帳號"
                placeholder="請輸入帳號"
                outlined
                required
                class="mt-6 input"
              ></v-text-field>

              <v-text-field
                v-model="password"
                :rules="passRules"
                type="password"
                label="密碼"
                placeholder="請輸入密碼"
                outlined
                required
                class="mt-4 input"
              ></v-text-field>

              <v-col cols="12" class="d-flex justify-between">
                <v-btn
                  class="btn_primary p-16 mr-8 mt-6"
                  @click="login"
                  :disabled="password.length === 0 || email.length === 0"
                >
                  Login
                </v-btn>
                <v-btn class="btn_secondary mt-6" @click="reset"> Reset </v-btn>
              </v-col>
            </v-form>
          </v-col>
        </v-col>
      </v-row>
    </v-container>
  </v-app>
</template>

<script>
// import axios from "axios";
export default {
  name: "Login",

  data() {
    return {
      email: "admin1",
      nameRules: [(v) => !!v || "帳號為必需填寫欄位"],
      password: "123456",
      passRules: [(v) => !!v || "密碼為必需填寫欄位"],
    };
  },
  methods: {
    login(e) {
      e.preventDefault();

      this.axios
        .post("https://tibamef2e.com/cfd104/g4/back/api/admin_login.php", {
          email: this.email,
          password: this.password,
        })
        .then((res) => {
          console.log(res);
          if (res.data.output == "success") {
            window.localStorage.setItem("authenticated", true);
            this.$router.push("/dashboard");
          } else {
            alert(res.data.noPerson);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    reset() {
      this.email = "";
      this.password = "";
    },
  },

  created() {
    if (window.localStorage.getItem("authenticated") === "true") {
      this.$router.push("/dashboard");
    }
  },
};
</script>

<style lang="scss" scoped>
@import "../../styles/app";

::v-deep .v-label {
  transform: translateX(-5px) translateY(-30px);
  height: 30px;
}

body {
  // background-color: #F6F7FF !important
}

.container {
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  padding: 0;
  // max-width: 1440px;

  /*左半部Scss*/

  .main_part {
    width: 100%;
    height: 100vh;
    background-color: rgb(228, 237, 248);
    display: flex;
    flex-direction: column;

    p {
      padding: 12px 0 0 22px;
      font-size: 20px;
      font-weight: 800;
    }

    .pic {
      width: 755px;
      height: 472px;
      position: absolute;
      overflow: hidden;
      bottom: 0;
      left: -50px;

      img {
        width: 90%;
        opacity: 0.5;
      }
    }
    .blue_ball {
      position: absolute;
      width: 100px;
      left: 29%;
      top: 35%;
      border: 8px solid #fff;
      border-radius: 50%;
    }
  }
}

/*右半部Scss*/

.login_part {
  position: relative;
  // background-color: red;
  .pic {
    width: 130px;
    height: 130px;
    position: absolute;
    top: 10px;
    right: 2%;
    img {
      width: 100%;
    }
  }
  .v-form {
    p {
      font-size: 12px;
    }
  }
}

/*CHANGE VUETIFY DEFAULT STYLE*/
.v-btn:not(.v-btn--round).v-size--default {
  min-width: 0;
  width: 45%;
}
.v-text-field input {
  padding-left: 5px;
}

.v-placeholder {
  top: 10px;
}
.v-text-field--filled > .v-input__control > .v-input__slot,
.v-text-field--full-width > .v-input__control > .v-input__slot,
.v-text-field--outlined > .v-input__control > .v-input__slot {
  min-height: 40px;
}

.login_part .v-input .v-label {
  line-height: 6px !important;
  left: -10px;
  top: 10px;
  height: 0;
}

@media screen and (max-width: 768px) {
  .form {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: 0px;
    position: relative;
    top: 40%;
    width: 80%;
  }
}
</style>







