<template>
  <v-container fluid>
    <div class="dashboard-page">
      <v-row no-gutters class="d-flex justify-space-between mt-10 mb-6">
        <h1 class="page-title">CIMI | DASHBOARD</h1>
      </v-row>

      <!---------------- 上方三個小卡片 ------------------->
      <v-row>
        <v-col
          cols="12"
          sm="6"
          md="4"
          class="pa-md-3 mb-md-5"
          v-for="(item, index) in mock.cards"
          :key="index"
        >
          <v-card class="mx-auto margin__none" outlined>
            <v-list-item three-line>
              <v-list-item-content>
                <div class="text-h6 mb-4">{{ item.title }}</div>
                <v-list-item-title class="text-h5 mb-2">
                  + {{ item.num }} {{ item.quan }}
                </v-list-item-title>
                <v-list-item-subtitle class="mt-1"
                  >{{ item.sub }} : {{ item.total }}
                  {{ item.quan }}</v-list-item-subtitle
                >
              </v-list-item-content>

              <v-list-item-avatar size="80" :color="item.color">
                <v-icon dark style="font-size: 50px"> {{ item.icon }} </v-icon>
              </v-list-item-avatar>
            </v-list-item>

            <v-card-actions>
              <v-btn text router-link :to="item.pages">
                {{ item.link }}
                <v-icon class="mdi-18px"> mdi-open-in-new </v-icon>
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>

      <!-- <v-row class="pa-md-3 mb-md-5">
        <v-divider></v-divider>
      </v-row>

      <v-row no-gutters class="d-flex justify-space-between mt-2">
        <h4 class="text-h6">CIMI | 後台首頁</h4>
      </v-row> -->

      <!---------------- 商城的CHART------------------->

      <v-row class="pa-xs-4 mt-5">
        <div class="col-12 col-md-6 pa-xs-4">
          <v-card class="col-12 col-md-6" outlined>
            <ApexChart
              ref="chart1"
              height="255"
              max-width="450"
              class="mt-1"
              :options="mock.shopChart.chartOptions"
              :series="mock.shopChart.series"
            ></ApexChart>
          </v-card>
        </div>
        <div class="col-12 col-md-6 pa-xs-4">
          <v-card class="col-12 col-md-6" outlined>
            <ApexChart
              ref="chart2"
              height="250"
              max-width="450"
              class="mt-1"
              :options="mock.apexChart2.chartOptions"
              :series="mock.apexChart2.series"
            ></ApexChart>
          </v-card>
        </div>
      </v-row>

      <!---------------- 檢舉的CHART ------------------->
      <v-row class="pa-3 mt-6">
        <v-card class="col-12" outlined>
          <ApexChart
            ref="chart3"
            max-width="900"
            height="350"
            :options="mock.apexChart3.chartOptions"
            :series="mock.apexChart3.series"
          ></ApexChart>
        </v-card>
      </v-row>
    </div>
  </v-container>
</template>





<script>
import mock from "./mock";
import Trend from "vuetrend";
import ApexChart from "vue-apexcharts";

export default {
  name: "Dashboard",
  components: {
    ApexChart,
    // VueC3
  },
  data() {
    return {
      mock,
      apexLoading: false,
      // mainApexAreaSelect: "Daily",
    };
  },

  methods: {},
  mounted() {
    setTimeout(() => {
      this.apexLoading = true;
      this.$refs.chart1.resetSeries();
      this.$refs.chart2.resetSeries();
      this.$refs.chart3.resetSeries();
    });
  },
};
</script>

<style src="./Dashboard.scss" lang="scss"/>

