import Vue from 'vue';
import Router from 'vue-router';

import Layout from '@/components/Layout/Layout';

// Pages
import Dashboard from '@/pages/Dashboard/Dashboard';
import Typography from '@/pages/Typography/Typography'
import Tables from '@/pages/Tables/Basic'
import Notifications from '@/pages/Notifications/Notifications'
import Icons from '@/pages/Icons/Icons'
import Charts from '@/pages/Charts/Charts'
import Maps from '@/pages/Maps/Google'
import Error from "@/pages/Error/Error";



/*celia's router */
import Login from "@/pages/Login/Login";
import Mems from "@/pages/Mems/Basic";
import Article from "@/pages/Article/Basic";
import Boards from "@/pages/Boards/Basic";
import Products from "@/pages/Products/Basic";
import Orders from "@/pages/Orders/Basic";
import ArticleReport from "@/pages/ArticleReport/Basic";
import CommentReport from "@/pages/CommentReport/Basic";
import GoodReport from "@/pages/GoodReport/Basic";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
    path: '/',
    redirect: 'login',
    name: 'Layout',
    component: Layout,
    children: [
      {
        path: 'dashboard',
        name: 'Dashboard',
        component: Dashboard,
      },
      {
        path: 'typography',
        name: 'Typography',
        component: Typography,
      },
      {
        path: 'tables',
        name: 'Tables',
        component: Tables
      },
      {
        path: 'notifications',
        name: 'Notifications',
        component: Notifications
      },
      {
        path: 'icons',
        name: 'Icons',
        component: Icons
      },
      {
        path: 'charts',
        name: 'Charts',
        component: Charts
      },
      {
        path: 'maps',
        name: 'Maps',
        component: Maps
      },
      {
        path: 'mems',
        name: 'Mems',
        component: Mems
      },
      {
        path: 'article',
        name: 'Article',
        component: Article
      },
      {
        path: 'boards',
        name: 'Boards',
        component: Boards
      },
      {
        path: 'products',
        name: 'Products',
        component: Products
      },
      {
        path: 'orders',
        name: 'Orders',
        component: Orders
      },
      {
        path: 'articlereport',
        name: 'ArticleReport',
        component: ArticleReport
      },
      {
        path: 'commentreport',
        name: 'CommentReport',
        component: CommentReport
      },
      {
        path: 'goodreport',
        name: 'GoodReport',
        component: GoodReport
      },
    ],
  },
    {
      path: '*',
      name: 'Error',
      component: Error
    }
  ],
});
