<template>
  <div class="Boards-page">
    <!-- 頁面標題 & 新增按鈕 -->
    <v-row no-gutters class="d-flex justify-space-between mt-10 mb-6">
      <h1 class="page-title">CIMI | 訂單管理</h1>
      <!-- 新增dialog -->
    </v-row>

    <!------------ 表格開始!------------>
    <v-data-table
      :headers="headers"
      :items="tableData"
      :search="search"
      sort-by="account"
      class="elevation-1"
    >
      <!-- 取貨方式slot: 0:宅配、1:超商取貨" -->
      <template v-slot:[`item.order_way`]="{ item }">
        <v-chip :color="wayColor(item.order_way)">
          {{ way(item.order_way) }}
        </v-chip>
      </template>

      <!-- 訂單狀態slot: 0:未出貨、1:已出貨、2:已收貨、3:訂單完成、4:已取消"-->
      <template v-slot:[`item.order_shipping`]="{ item }">
        <v-chip :color="shipColor(item.order_shipping)">
          {{ ship(item.order_shipping) }}
        </v-chip>
      </template>

      <!-- 編輯的SLOT -->
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
        <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
      </template>

      <!-- 頂部文章列表的SLOT -->
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>訂單列表</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>

          <!-- 搜尋框 -->
          <v-text-field
            hide-details
            append-icon="mdi-magnify"
            single-line
            class="shrink pr-6"
            v-model="search"
            label="Search"
          ></v-text-field>

          <!-- 下載CSV -->
          <download-csv :data="tableData">
            <v-btn icon>
              <v-icon class="mdi-36px pl-2 pr-2">
                mdi-arrow-down-bold-box
              </v-icon>
            </v-btn>
          </download-csv>

          <!-- 編輯dialog -->
          <v-dialog
            v-model="dialog"
            @save="save"
            @cancel="cancel"
            max-width="400"
          >
            <v-card class="mx-auto" max-width="450" tile>
              <v-card-title>
                <span class="text-h5">訂單資訊</span>
              </v-card-title>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-alert-circle-outline</v-icon>
                  <span class="text-subtitle-1">訂單編號 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.order_no }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-calendar-clock</v-icon>
                  <span class="text-subtitle-1">訂購時間 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.order_time }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-account</v-icon>
                  <span class="text-subtitle-1">訂購者會員編號 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.order_buyer_no }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-account</v-icon>
                  <span class="text-subtitle-1">訂購者會員名稱 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.mem_name }}
                </div>
              </div>

              <v-divider></v-divider>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-cash-100</v-icon>
                  <span class="text-subtitle-1">訂單總價 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.order_tp }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-minus</v-icon>
                  <span class="text-subtitle-1">積分折扺 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.order_price_minus }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-equal</v-icon>
                  <span class="text-subtitle-1">實際金額(打折後) :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.order_real_price }}
                </div>
              </div>

              <v-divider></v-divider>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-truck</v-icon>
                  <span class="text-subtitle-1">配送資訊 :</span>
                </div>
                <div class="meminfo">
                  <v-chip :color="wayColor(editedItem.order_shipping)">
                    {{ way(editedItem.order_way) }}
                  </v-chip>
                </div>
              </div>

              <!-- <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-message-alert</v-icon>
                  <span class="text-subtitle-1">取貨地點 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.order_place }}
                </div>
              </div> -->

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-message-alert</v-icon>
                  <span class="text-subtitle-1">訂單狀態 :</span>
                </div>
                <div class="meminfo">
                  <v-chip :color="shipColor(editedItem.order_shipping)">
                    {{ ship(editedItem.order_shipping) }}
                  </v-chip>
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-thumb-up</v-icon>
                  <span class="text-subtitle-1">訂單滿意度 :</span>
                </div>
                <div class="meminfo">
                  {{ satisfy(editedItem.order_satisfy) }}
                </div>
              </div>

              <v-spacer></v-spacer>

              <div class="btn">
                <v-btn color="blue darken-1" text @click="close"> 關閉 </v-btn>
              </div>
            </v-card>
          </v-dialog>

          <!-- 刪除dialog -->
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">
                確認刪除此筆訂單資訊 ?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  >取消</v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  >確認</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
    </v-data-table>
    <!----------- 表格結束!------------>
  </div>
</template>

<script>
export default {
  inject: ["reload"], //新增刪除讓頁面重整使用!
  data: () => ({
    search: "", // 搜尋列的v-model
    dialog: false, // 控制EDIT DIALOG 顯示或是不顯示
    dialogDelete: false, // 控制DELETE DIALOG 顯示或是不顯示
    dialogNew: false,
    tableData: [], // 表格的所有資料
    mems: [],
    headers: [
      // 表格的欄位名稱
      {
        text: "訂單編號",
        align: "start",
        value: "order_no",
      },
      { text: "訂購時間", value: "order_time" },
      { text: "會員編號 - 訂購者", value: "order_buyer_no" },
      { text: "會員名稱 - 訂購者", value: "mem_name" },
      { text: "配送資訊", value: "order_way" },
      { text: "訂單狀態", value: "order_shipping" },
      { text: "編輯", value: "actions", sortable: false }, // sortable => 此欄位是否需要排序
    ],
    boards: [],
    manu: [],
    order_way: [
      // 控制編輯文章下拉選單的 val & item
      { val: 0, item: "宅配" },
      { val: 1, item: "超商取貨" },
    ],
    order_shipping: [
      // 控制編輯文章下拉選單的 val & item
      { val: 0, item: "未出貨" },
      { val: 1, item: "已出貨" },
      { val: 2, item: "已收貨" },
      { val: 3, item: "訂單完成" },
      { val: 4, item: "已取消" },
    ],
    editedIndex: -1, //  控制目前點選編輯或是刪除該物件之index
    editedItem: {
      //  控制目前點選編輯或是刪除該筆物件之內容
      order_no: "美妝",
      order_time: null,
      order_buyer_no: "",
      mem_name: "",
      order_way: "",
      order_shipping: "0",
      order_tp: "0",
      order_price_minus: "0",
      order_real_price: "0",
      order_place: "0",
      order_over_time: "0",
      order_satisfy: "0",
    },
    defaultItem: {
      //  初始值
      order_no: "美妝",
      order_time: null,
      order_buyer_no: "",
      mem_name: "",
      order_way: "",
      order_shipping: "0",
      order_tp: "0",
      order_price_minus: "0",
      order_real_price: "0",
      order_place: "0",
      order_over_time: "0",
      order_satisfy: "0",
    },
    time: "", //  FOR 文章發布日期  => 但是有 Bug QQ
  }),
  mounted() {
    //  從PHP執行sql, 獲取表格所需的資料
    this.axios
      .get("https://tibamef2e.com/cfd104/g4/back/api/orders.php")
      .then((res) => {
        this.tableData = res.data;
        let cate = res.data.map((item) => Object.values(item)[7]);
        let cateArr = cate.filter((ele, pos) => cate.indexOf(ele) == pos);
        this.boards = cateArr;

        let manu = res.data.map((item) => Object.values(item)[6]);
        let manuArr = manu.filter((ele, pos) => manu.indexOf(ele) == pos);
        this.manu = manuArr;
      })
      .catch((err) => {
        console.log(err);
      });
  },
  computed: {},
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  created() {
    this.initialize();
  },
  methods: {
    initialize() {
      this.tableData = [
        {
          name: "美妝",
          account: "2022-02-27",
          access: "7pupu",
          status: "異常",
        },
        {
          name: "感情",
          account: "2022-02-26",
          access: "抹茶拿鐵",
          status: "正常",
        },
      ];
    },
    way: (v) => {
      // 因資料拿回的狀態是0/1 -> 所以將其轉換成相對應文字
      if (v == 0) {
        return "宅配";
      } else if (v == 1) {
        return "超商取貨";
      }
    },
    wayColor(state) {
      //用於控制 state 的 v-chip 顏色
      if (state == 1) {
        return "default";
      } else if (state == 0) {
        return "primary";
      }
    },
    ship: (v) => {
      // 因資料拿回的狀態是0/1 -> 所以將其轉換成相對應文字
      if (v == 0) {
        return "未出貨";
      } else if (v == 1) {
        return "已出貨";
      } else if (v == 2) {
        return "已收貨";
      } else if (v == 3) {
        return "訂單完成";
      } else {
        return "已取消";
      }
    },
    shipColor(v) {
      //用於控制 ship 的 v-chip 顏色
      if (v == 0) {
        return "secondary";
      } else if (v == 1) {
        return "warning";
      } else if (v == 2) {
        return "primary";
      } else if (v == 3) {
        return "success";
      } else {
        return "dark";
      }
    },

    satisfy(v) {
      //用於控制 滿意度有幾顆星星 的 v-chip 顏色
      if (v == 1) {
        return "👍";
      } else if (v == 2) {
        return "👍👍";
      } else if (v == 3) {
        return "👍👍👍";
      } else if (v == 4) {
        return "👍👍👍👍";
      } else {
        return "👍👍👍👍👍";
      }
    },

    editItem(item) {
      //編輯物件
      this.editedIndex = this.tableData.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      //刪除物件
      this.editedIndex = this.tableData.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      let params = new URLSearchParams();
      params.append("order_no", this.editedItem.order_no);
      params.append("phpState", "delete");
      this.axios
        .post("https://tibamef2e.com/cfd104/g4/back/api/editorder.php", params)
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
      this.tableData.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.dialogNew = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    save() {
      //for 修改
      if (this.editedIndex > -1) {
        Object.assign(this.tableData[this.editedIndex], this.editedItem);
        let params = new URLSearchParams();
        params.append("goods_class", this.editedItem.goods_class);
        params.append("goods_name", this.editedItem.goods_name);
        params.append("goods_i", this.editedItem.goods_i);
        params.append("goods_price", this.editedItem.goods_price);
        params.append("goods_firm", this.editedItem.goods_firm);
        params.append("goods_st", this.editedItem.goods_st);
        params.append("goods_no", this.editedItem.goods_no);
        params.append("phpState", "renew");
        this.axios
          .post("https://tibamef2e.com/cfd104/g4/back/api/editgood.php", params)
          .then((res) => {
            console.log(res);
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        return false;
      }
      this.close();
    },
  },
};
</script>

<style lang="scss" scoped>
.txt {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 36px 0 24px;
  margin: 10px 0;
}
.btn {
  display: flex;
  justify-content: flex-end;
  padding: 20px 20px 10px 0;
}
</style>