<template>
  <div class="Boards-page">
    <!-- 頁面標題 & 新增按鈕 -->
    <v-row no-gutters class="d-flex justify-space-between mt-10 mb-6">
      <h1 class="page-title">CIMI | 商品管理</h1>
      <!-- 新增dialog -->
      <v-dialog v-model="dialogNew" persistent max-width="500px">
        <template v-slot:activator="{ on, attrs }">
          <v-btn color="primary" dark v-bind="attrs" v-on="on"> 新增 </v-btn>
        </template>

        <v-card>
          <v-card-title>
            <span class="text-h5">🛴管理員新增商品</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12">
                  <v-select
                    v-model="editedItem.goods_class"
                    label="請選擇商品類別"
                    :items="categories"
                    prepend-icon="mdi-format-align-left"
                  >
                  </v-select>
                </v-col>

                <v-col cols="12">
                  <v-select
                    v-model="editedItem.goods_firm"
                    label="請選擇來源廠商"
                    :items="manu"
                    prepend-icon="mdi-clipboard-flow"
                  >
                  </v-select>
                </v-col>

                <v-col cols="12" sm="12" md="12">
                  <v-text-field
                    label="請輸入商品名稱"
                    v-model="editedItem.goods_name"
                    prepend-icon="mdi-basket"
                  >
                  </v-text-field>
                </v-col>

                <v-col cols="12" sm="12" md="12">
                  <v-text-field
                    label="請輸入商品價格"
                    v-model="editedItem.goods_price"
                    prepend-icon="mdi-cash-100"
                  >
                  </v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-textarea
                    filled
                    auto-grow
                    label="🖊️請輸入商品描述"
                    rows="10"
                    row-height="15"
                    v-model="editedItem.goods_i"
                  ></v-textarea>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="dialogNew = false">
              取消
            </v-btn>
            <v-btn color="blue darken-1" text @click="createNew"> 保存 </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>

    <!------------ 表格開始!------------>
    <v-data-table
      :headers="headers"
      :items="tableData"
      :search="search"
      sort-by="account"
      class="cell-height"
      dense
    >
      <!-- 商品狀態slot: 正常看板以及異常看板有不同顏色 -->
      <template v-slot:[`item.goods_st`]="{ item }">
        <v-chip :color="getColor(item.goods_st)" dark>
          {{ State(item.goods_st) }}
        </v-chip>
      </template>

      <!-- 商品類別slot:  -->
      <template v-slot:[`item.goods_class`]="{ item }">
        <v-chip :color="primary">
          {{ item.goods_class }}
        </v-chip>
      </template>

      <!-- 商品介紹slot: 正常看板以及異常看板有不同顏色 -->
      <!-- <template v-slot:[`item.goods_i`]="{ item }">
        <tr>
          <td class="truncate">{{ item.goods_i }}</td>
        </tr>
      </template> -->

      <!-- 商品圖片slot: 正常看板以及異常看板有不同顏色 -->
      <template v-slot:[`item.goods_pic1`]="{ item }">
        <tr>
          <td>
            <div class="img">
              <img :src="adjust(item.goods_pic1)" />
            </div>
          </td>
        </tr>
      </template>

      <!-- <img src="../../assets/images/goods/goods_sort_page_2.jpg" alt=""> -->

      <!-- 編輯的SLOT -->
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
        <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
      </template>

      <!-- 頂部文章列表的SLOT -->
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>商品列表</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>

          <!-- 搜尋框 -->
          <v-text-field
            hide-details
            append-icon="mdi-magnify"
            single-line
            class="shrink pr-6"
            v-model="search"
            label="Search"
          ></v-text-field>

          <!-- 下載CSV -->
          <download-csv :data="tableData">
            <v-btn icon title="Download CSV">
              <v-icon class="mdi-36px pl-2 pr-2">
                mdi-arrow-down-bold-box
              </v-icon>
            </v-btn>
          </download-csv>

          <!-- 編輯dialog -->
          <v-dialog
            v-model="dialog"
            @save="save"
            @cancel="cancel"
            max-width="450"
          >
            <v-card class="mx-auto" max-width="450" tile>
              <v-card-title>
                <span class="text-h5">商品資訊</span>
              </v-card-title>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-alert-circle-outline</v-icon>
                  <span class="text-subtitle-1">商品ID :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.goods_no }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-basket</v-icon>
                  <span class="text-subtitle-1">商品名稱 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.goods_name }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-format-align-left</v-icon>
                  <span class="text-subtitle-1">商品類別 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.goods_class }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-clipboard-flow</v-icon>
                  <span class="text-subtitle-1">來源廠商 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.goods_firm }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-message-alert</v-icon>
                  <span class="text-subtitle-1">商品狀態 :</span>
                </div>
                <div class="meminfo">
                  <v-select
                    :items="states"
                    item-text="item"
                    item-value="val"
                    v-model="editedItem.goods_st"
                    width="80px"
                    class="selector"
                  ></v-select>
                </div>
              </div>

              <div class="pr-3 pl-3">
                <v-col cols="12" sm="12" md="12">
                  <v-text-field
                    label="請輸入商品價格"
                    v-model="editedItem.goods_price"
                    prepend-icon="mdi-cash-100"
                  >
                  </v-text-field>
                </v-col>
              </div>

              <div class="article_content">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-format-annotation-plus</v-icon>
                  <span class="text-subtitle-1">商品描述 :</span>
                </div>
                <div class="meminfo">
                  <v-textarea
                    :value="editedItem.goods_i"
                    filled
                    auto-grow
                  ></v-textarea>
                </div>
              </div>

              <v-spacer></v-spacer>

              <div class="btn">
                <v-btn color="blue darken-1" text @click="dialog = false"
                  >取消</v-btn
                >
                <v-btn color="blue darken-1" text @click="save(editedIndex)"
                  >確認</v-btn
                >
              </div>
            </v-card>
          </v-dialog>

          <!-- 刪除dialog -->
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">
                確認刪除此筆看板資訊 ?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  >取消</v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  >確認</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
    </v-data-table>
    <!----------- 表格結束!------------>
  </div>
</template>

<script>
export default {
  inject: ["reload"], //新增刪除讓頁面重整使用!
  data: () => ({
    search: "", // 搜尋列的v-model
    dialog: false, // 控制EDIT DIALOG 顯示或是不顯示
    dialogDelete: false, // 控制DELETE DIALOG 顯示或是不顯示
    dialogNew: false,
    tableData: [], // 表格的所有資料
    // cate: ["3C家電", "美食補給", "質感生活", "風格穿搭", "運動健身", "美妝保養"],

    headers: [
      // 表格的欄位名稱
      { text: "ID", value: "goods_no" },
      { text: "商品圖片", value: "goods_pic1" },
      { text: "商品名稱", value: "goods_name" },
      {
        text: "商品類別",
        align: "start",
        value: "goods_class",
      },

      { text: "來源廠商", value: "goods_firm" },
      { text: "商品價格", value: "goods_price" },
      { text: "商品狀態", value: "goods_st" },
      { text: "編輯", value: "actions", sortable: false }, // sortable => 此欄位是否需要排序
    ],
    categories: [],
    manu: [],
    imgUrl: [],

    states: [
      // 控制編輯文章下拉選單的 val & item
      { val: 0, item: "正常" },
      { val: 1, item: "下架" },
      { val: 2, item: "缺貨" },
    ],
    editedIndex: -1, //  控制目前點選編輯或是刪除該物件之index
    editedItem: {
      //  控制目前點選編輯或是刪除該筆物件之內容
      goods_class: "美妝",
      goods_name: null,
      goods_pic1: "",
      goods_i: "",
      goods_price: "",
      goods_firm: "0",
      goods_st: "0",
      goods_no: "0",
    },
    defaultItem: {
      //  初始值
      goods_class: "美妝",
      goods_name: null,
      goods_pic1: "",
      goods_i: "",
      goods_price: "0",
      goods_firm: "0",
      goods_st: "0",
      goods_no: "0",
    },
    time: "", //  FOR 文章發布日期  => 但是有 Bug QQ
  }),
  mounted() {
    //  從PHP執行sql, 獲取表格所需的資料
    this.axios
      .get("https://tibamef2e.com/cfd104/g4/back/api/goods.php")
      .then((res) => {
        this.tableData = res.data.goods;

        let s1 = "../../../../";
        let s2 = res.data.img;

        let s3 = [];
        for (let i = 0; i < s2.length; i++) {
          s3[i] = s1.concat(s2[i].goods_pic1);
        }
        this.imgUrl = s3;

        console.log(this.imgUrl);

        // console.table(s3);

        let url = res.data.goods.map((item) => Object.values(item)[3]);
        this.imgUrl = url;

        let cate = res.data.goods.map((item) => Object.values(item)[7]);
        let cateArr = cate.filter((ele, pos) => cate.indexOf(ele) == pos);
        this.categories = cateArr;

        let manu = res.data.goods.map((item) => Object.values(item)[6]);
        let manuArr = manu.filter((ele, pos) => manu.indexOf(ele) == pos);
        this.manu = manuArr;
      })
      .catch((err) => {
        console.log(err);
      });
  },
  computed: {},
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  created() {
    this.initialize();
  },
  methods: {
    initialize() {
      this.tableData = [
        {
          name: "美妝",
          account: "2022-02-27",
          access: "7pupu",
          status: "異常",
        },
        {
          name: "感情",
          account: "2022-02-26",
          access: "抹茶拿鐵",
          status: "正常",
        },
      ];
    },
    adjust(item) {
      // let url = this.item.goods_pic1;
      let route = "../";
      // let route = ''
      let path = route.concat(item);
      return path;
    },
    State: (v) => {
      // 因資料拿回的狀態是0/1 -> 所以將其轉換成相對應文字
      if (v == 0) {
        return "正常";
      } else if (v == 1) {
        return "下架";
      } else {
        return "缺貨";
      }
    },
    getColor(state) {
      //用於控制 state 的 v-chip 顏色
      if (state == 1) {
        return "default";
      } else if (state == 0) {
        return "primary";
      } else {
        return "secondary";
      }
    },

    editItem(item) {
      //編輯物件
      this.editedIndex = this.tableData.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
      this.adjust();
    },

    deleteItem(item) {
      //刪除物件
      this.editedIndex = this.tableData.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      let params = new URLSearchParams();
      params.append("goods_no", this.editedItem.goods_no);
      params.append("phpState", "delete");
      this.axios
        .post("https://tibamef2e.com/cfd104/g4/back/api/editgood.php", params)
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
      this.tableData.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.dialogNew = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    save() {
      //for 修改
      if (this.editedIndex > -1) {
        Object.assign(this.tableData[this.editedIndex], this.editedItem);
        let params = new URLSearchParams();
        params.append("goods_class", this.editedItem.goods_class);
        params.append("goods_name", this.editedItem.goods_name);
        params.append("goods_i", this.editedItem.goods_i);
        params.append("goods_price", this.editedItem.goods_price);
        params.append("goods_firm", this.editedItem.goods_firm);
        params.append("goods_st", this.editedItem.goods_st);
        params.append("goods_no", this.editedItem.goods_no);
        params.append("phpState", "renew");
        this.axios
          .post("https://tibamef2e.com/cfd104/g4/back/api/editgood.php", params)
          .then((res) => {
            console.log(res);
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        return false;
      }
      this.close();
    },
    createNew() {
      //for 新增
      let params = new URLSearchParams();
      params.append("goods_class", this.editedItem.goods_class);
      params.append("goods_name", this.editedItem.goods_name);
      params.append("goods_i", this.editedItem.goods_i);
      params.append("goods_price", this.editedItem.goods_price);
      params.append("goods_firm", this.editedItem.goods_firm);
      params.append("goods_st", this.editedItem.goods_st);
      params.append("phpState", "createNew");
      this.axios
        .post("https://tibamef2e.com/cfd104/g4/back/api/editgood.php", params)
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
      this.tableData.push(this.editedItem);
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
      this.close();
      this.reload();
    },
  },
};
</script>

<style scoped>
::v-deep .v-label {
  /* transform: translateY(-20px); */
  height: 30px;
}
.txt {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 36px 0 24px;
  margin: 10px 0;
}
.article_content {
  padding: 0 36px 0 24px;
  margin: 10px 0;
}
.selector {
  width: 150px;
}
.btn {
  display: flex;
  justify-content: space-evenly;
  padding-bottom: 20px;
}
.img {
  max-width: 150px;
  width: 150px;
  height: 150px;
  padding: 10px;
}
img{
  width: 100%;
}
</style>