<template>
  <div class="Boards-page">
    <!-- 頁面標題 & 新增按鈕 -->
    <v-row no-gutters class="d-flex justify-space-between mt-10 mb-6">
      <h1 class="page-title">CIMI | 文章管理</h1>
      <!-- 新增dialog -->

      <v-dialog v-model="dialogNew" persistent max-width="500px">
        <template v-slot:activator="{ on, attrs }">
          <v-btn color="primary" dark v-bind="attrs" v-on="on"> 新增 </v-btn>
        </template>

        <v-card>
          <v-card-title>
            <span class="text-h5">🙋管理員新增文章</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12">
                  <v-select
                    v-model="editedItem.billboard_no"
                    label="看板名稱"
                    :items="boards"
                    item-text="item"
                    item-value="val"
                    prepend-icon="mdi-trello"
                  >
                  </v-select>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field
                    label="請輸入文章標題"
                    v-model="editedItem.article_title"
                    prepend-icon="mdi-format-annotation-plus"
                  >
                  </v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-textarea
                    filled
                    auto-grow
                    label="🖊️請輸入內容"
                    rows="10"
                    row-height="30"
                    v-model="editedItem.article_content"
                  ></v-textarea>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="dialogNew = false">
              取消
            </v-btn>
            <v-btn color="blue darken-1" text @click="createNew"> 保存 </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>

    <!------------ 表格開始!------------>
    <v-data-table
      :headers="headers"
      :items="tableData"
      :search="search"
      sort-by="account"
      class="elevation-1"
    >
      <!-- 文章狀態slot: 正常看板以及異常看板有不同顏色 -->
      <template v-slot:[`item.article_state`]="{ item }">
        <v-chip :color="getColor(item.article_state)" dark>
          {{ State(item.article_state) }}
        </v-chip>
      </template>

      <!-- 看板名稱slot:  -->
      <template v-slot:[`item.billboard_no`]="{ item }">
        <!-- <v-chip class="ma-2" color="primary" outlined> -->
        <span>{{ item.billboard_icon }}</span>
        {{ billboards(item.billboard_no) }}
        <!-- </v-chip> -->
      </template>

      <!-- 編輯的SLOT -->
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
        <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
      </template>

      <!-- 頂部文章列表的SLOT -->
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>文章列表</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>

          <!-- 搜尋框 -->
          <v-text-field
            hide-details
            append-icon="mdi-magnify"
            single-line
            class="shrink pr-6"
            v-model="search"
            label="Search"
          ></v-text-field>

          <!-- 下載CSV -->
          <download-csv :data="tableData">
            <v-btn icon>
              <v-icon class="mdi-36px pl-2 pr-2">
                mdi-arrow-down-bold-box
              </v-icon>
            </v-btn>
          </download-csv>

          <!-- 編輯dialog -->
          <v-dialog
            v-model="dialog"
            @save="save"
            @cancel="cancel"
            max-width="450"
          >
            <v-card class="mx-auto" max-width="450" tile>
              <v-card-title>
                <span class="text-h5">文章資訊</span>
              </v-card-title>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-format-align-left</v-icon>
                  <span class="text-subtitle-1">看板名稱 :</span>
                </div>
                <div class="meminfo">
                  {{ billboards(editedItem.billboard_no) }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-format-align-left</v-icon>
                  <span class="text-subtitle-1">文章標題 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.article_title }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-calendar-clock</v-icon>
                  <span class="text-subtitle-1">發布時間 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.publish_date }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-account-multiple</v-icon>
                  <span class="text-subtitle-1">發文者 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.mem_name }}
                </div>
              </div>

              <div class="article_content">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-format-annotation-plus</v-icon>
                  <span class="text-subtitle-1">文章內容 :</span>
                </div>
                <div class="meminfo">
                  <v-textarea
                    :value="editedItem.article_content"
                    filled
                    auto-grow
                    disabled
                  ></v-textarea>
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-format-annotation-plus</v-icon>
                  <span class="text-subtitle-1">文章狀態 :</span>
                </div>
                <div class="meminfo">
                  <v-select
                    :items="states"
                    item-text="item"
                    item-value="val"
                    v-model="editedItem.article_state"
                    width="80px"
                    class="selector"
                  ></v-select>
                </div>
              </div>

              <v-spacer></v-spacer>

              <div class="btn">
                <v-btn color="blue darken-1" text @click="dialog = false"
                  >取消</v-btn
                >
                <v-btn color="blue darken-1" text @click="save(editedIndex)"
                  >確認</v-btn
                >
              </div>
            </v-card>
          </v-dialog>

          <!-- 刪除dialog -->
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">
                確認刪除此筆文章資訊 ?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  >取消</v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  >確認</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
    </v-data-table>
    <!----------- 表格結束!------------>
  </div>
</template>

<script>
export default {
  inject: ["reload"],
  data: () => ({
    search: "", // 搜尋列的v-model
    dialog: false, // 控制EDIT DIALOG 顯示或是不顯示
    dialogDelete: false, // 控制DELETE DIALOG 顯示或是不顯示
    dialogNew: false,
    tableData: [], // 表格的所有資料
    headers: [
      // 表格的欄位名稱
      {
        text: "所屬看板",
        align: "start",
        value: "billboard_no",
      },
      { text: "創建時間", value: "publish_date" },
      { text: "文章標題", value: "article_title" },
      { text: "發文者", value: "mem_name" },
      { text: "文章狀態", value: "article_state" },
      { text: "編輯", value: "actions", sortable: false }, // sortable => 此欄位是否需要排序
    ],
    boards: [
      // 控制編輯文章下拉選單的 val & item
      { val: 1, item: "好聽音樂" },
      { val: 2, item: "旅遊踏青" },
      { val: 3, item: "美妝保養" },
      { val: 4, item: "情感生活" },
      { val: 5, item: "穿搭時尚" },
      { val: 6, item: "運動健身" },
    ],
    states: [
      // 控制編輯文章下拉選單的 val & item
      { val: 0, item: "正常" },
      { val: 1, item: "異常" },
    ],
    editedIndex: -1, //  控制目前點選編輯或是刪除該物件之index
    editedItem: {
      //  控制目前點選編輯或是刪除該筆物件之內容
      billboard_no: "選擇看板名稱",
      publish_date: null,
      article_title: "",
      article_state: "0",
      article_content: "",
    },
    defaultItem: {
      //  初始值
      billboard_no: "選擇看板名稱",
      publish_date: null,
      article_title: "",
      article_state: "0",
      article_content: "",
    },
    // today:this.editDate,
  }),
  mounted() {
    //  從PHP執行sql, 獲取表格所需的資料
    this.axios
      .get("https://tibamef2e.com/cfd104/g4/back/api/articles.php")
      .then((res) => {
        console.log(res);
        this.tableData = res.data;
        // this.boards = res.data.map((item) => Object.values(item)[0]);
      })
      .catch((err) => {
        console.log(err);
      });
  },
  computed: {
    formTitle() {
      // 控制新增或編輯之dialog => if true new item, else edit item
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
    // editDate() {
    //   let yyyy = new Date().getFullYear()
    //   let mm = new Date().getMonth()+1
    //   let dd = new Date().getDate()
    //   let hour = new Date().getHours()
    //   let min = new Date().getMinutes()
    //   let sec = new Date().getSeconds()
    //   if (dd < 10) {
    //     dd = "0" + dd;
    //   }
    //   if (mm < 10) {
    //     mm = "0" + mm;
    //   }
    //   if (hour < 10) {
    //     hour = "0" + hour;
    //   }
    //   if (min < 10) {
    //     min = "0" + min;
    //   }
    //   if (sec < 10) {
    //     sec = "0" + sec;
    //   }
    //   return yyyy + "-" + mm + "-" + dd + " " + hour + ":" + min + ":" + sec;
    // },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  created() {
    this.initialize();
  },
  methods: {
    initialize() {
      this.tableData = [
        {
          name: "美妝",
          account: "2022-02-27",
          access: "7pupu",
          status: "異常",
        },
        {
          name: "感情",
          account: "2022-02-26",
          access: "抹茶拿鐵",
          status: "正常",
        },
      ];
    },
    State: (v) => {
      // 因資料拿回的狀態是0/1 -> 所以將其轉換成相對應文字
      if (v == 0) {
        return "正常";
      } else {
        return "異常";
      }
    },

    billboards: (v) => {
      // 因資料拿回的看板是1-6 -> 所以將其轉換成相對應文字
      if (v == 1) {
        return "好聽音樂";
      } else if (v == 2) {
        return "旅遊踏青";
      } else if (v == 3) {
        return "美妝保養";
      } else if (v == 4) {
        return "情感生活";
      } else if (v == 5) {
        return "穿搭時尚";
      } else {
        return "運動健身";
      }
    },

    getColor(state) {
      //用於控制 state 的 v-chip 顏色
      if (state == 1) return "default";
      else if (state == 0) return "primary";
    },

    editItem(item) {
      //編輯物件
      this.editedIndex = this.tableData.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      //刪除物件
      this.editedIndex = this.tableData.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      let params = new URLSearchParams();
      params.append("article_no", this.editedItem.article_no);
      params.append("phpState", "delete");
      this.axios
        .post("https://tibamef2e.com/cfd104/g4/back/api/editarticle.php", params)
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
      this.tableData.splice(this.editedIndex, 1);
      this.close();
    },

    close() {
      this.dialog = false;
      this.dialogNew = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
      this.close();
    },
    save() {
      //for 修改
      if (this.editedIndex > -1) {
        Object.assign(this.tableData[this.editedIndex], this.editedItem);
        let params = new URLSearchParams();
        params.append("article_no", this.editedItem.article_no);
        params.append("article_state", this.editedItem.article_state);
        params.append("article_title", this.editedItem.article_title);
        params.append("billboard_no", this.editedItem.billboard_no);
        params.append("last_edit_date", this.editedItem.publish_date);
        params.append("phpState", "renew");
        this.axios
          .post("https://tibamef2e.com/cfd104/g4/back/api/editarticle.php", params)
          .then((res) => {
            console.log(res);
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        return false;
      }
      this.close();
    },
    createNew() {
      //for 新增
      this.tableData.push(this.editedItem);
      let params = new URLSearchParams();
      params.append("article_content", this.editedItem.article_content);
      params.append("article_state", this.editedItem.article_state);
      params.append("article_title", this.editedItem.article_title);
      params.append("billboard_no", this.editedItem.billboard_no);
      params.append("phpState", "createNew");
      this.axios
        .post("https://tibamef2e.com/cfd104/g4/back/api/editarticle.php", params)
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
      this.close();
      this.reload();
    },
  },
};
</script>
<style scoped>
::v-deep .v-label {
  /* transform: translateY(-20px); */
  height: 30px;
}
.txt {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 36px 0 24px;
  margin: 10px 0;
}
.article_content {
  padding: 0 36px 0 24px;
  margin: 10px 0;
}
.selector {
  width: 150px;
}
.btn {
  display: flex;
  justify-content: space-evenly;
  padding-bottom: 20px;
}
</style>