<template>
  <div class="Boards-page">
    <!-- 頁面標題 -->
    <v-row no-gutters class="d-flex justify-space-between mt-10 mb-6">
      <h1 class="page-title">CIMI | 文章檢舉管理</h1>
    </v-row>

    <!------------ 表格開始!------------>
    <v-data-table
      :headers="headers"
      :items="tableData"
      :search="search"
      sort-by="account"
      class="elevation-1"
    >
      <!-- 檢舉處理狀態slot: 0:處理中 1:已審核 -->
      <template v-slot:[`item.report_state`]="{ item }">
        <v-chip :color="getColor(item.report_state)">
          {{ State(item.report_state) }}
        </v-chip>
      </template>

      <!-- 處理措施slot: 0:未審核 1:通過 2:未通過 -->
      <template v-slot:[`item.report_report`]="{ item }">
        <v-chip :color="reportColor(item.report_report)">
          {{ report(item.report_report) }}
        </v-chip>
      </template>

      <!-- 編輯的SLOT -->
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
        <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
      </template>

      <!-- 頂部文章列表的SLOT -->
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>文章檢舉列表</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>

          <!-- 搜尋框 -->
          <v-text-field
            hide-details
            append-icon="mdi-magnify"
            single-line
            class="shrink pr-6"
            v-model="search"
            label="Search"
          ></v-text-field>

          <!-- 下載CSV -->
          <download-csv :data="tableData">
            <v-btn icon title="Download CSV">
              <v-icon class="mdi-36px pl-2 pr-2">
                mdi-arrow-down-bold-box
              </v-icon>
            </v-btn>
          </download-csv>

          <!-- 編輯dialog -->
          <v-dialog
            v-model="dialog"
            @save="save"
            @cancel="cancel"
            max-width="450"
          >
            <v-card class="mx-auto" max-width="450" tile>
              <v-card-title>
                <span class="text-h5">文章資訊</span>
              </v-card-title>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-format-align-left</v-icon>
                  <span class="text-subtitle-1">文章標題 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.article_title }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-calendar-clock</v-icon>
                  <span class="text-subtitle-1">檢舉時間 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.report_date }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-sim-alert</v-icon>
                  <span class="text-subtitle-1">檢舉事由 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.report_reason }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-account-alert</v-icon>
                  <span class="text-subtitle-1">檢舉者 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.mem_name }}
                </div>
              </div>

              <div class="article_content">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-comment-text</v-icon>
                  <span class="text-subtitle-1">檢舉留言內容 :</span>
                </div>
                <div class="meminfo">
                  <v-textarea
                    :value="editedItem.article_content"
                    filled
                  ></v-textarea>
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-message-alert</v-icon>
                  <span class="text-subtitle-1">檢舉處理狀態 :</span>
                </div>
                <div class="meminfo">
                  <v-select
                    :items="stateSelect"
                    item-text="item"
                    item-value="val"
                    v-model="editedItem.report_report"
                    width="80px"
                    class="selector"
                  ></v-select>
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-message-alert</v-icon>
                  <span class="text-subtitle-1">處理措施 :</span>
                </div>
                <div class="meminfo">
                  <v-select
                    :items="reportSelect"
                    item-text="item"
                    item-value="val"
                    v-model="editedItem.report_report"
                    width="80px"
                    class="selector"
                  ></v-select>
                </div>
              </div>

              <v-spacer></v-spacer>

              <div class="btn">
                <v-btn color="blue darken-1" text @click="dialog = false"
                  >取消</v-btn
                >
                <v-btn color="blue darken-1" text @click="save(editedIndex)"
                  >確認</v-btn
                >
              </div>
            </v-card>
          </v-dialog>

          <!-- 刪除dialog -->
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">
                確認刪除此筆文章檢舉資訊 ?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  >取消</v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  >確認</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
    </v-data-table>
    <!----------- 表格結束!------------>
  </div>
</template>

<script>
export default {
  inject: ["reload"], //新增刪除讓頁面重整使用!
  data: () => ({
    search: "", // 搜尋列的v-model
    dialog: false, // 控制EDIT DIALOG 顯示或是不顯示
    dialogDelete: false, // 控制DELETE DIALOG 顯示或是不顯示
    tableData: [], // 表格的所有資料
    headers: [
      // 表格的欄位名稱
      {
        text: "文章編號",
        align: "start",
        value: "article_no",
      },
      { text: "文章標題", value: "article_title" },
      { text: "檢舉時間", value: "report_date" },
      { text: "檢舉事由", value: "report_reason" },
      { text: "檢舉者 ", value: "mem_name" },

      { text: "檢舉處理狀態", value: "report_state" },
      { text: "處理措施", value: "report_report" },
      { text: "編輯", value: "actions", sortable: false }, // sortable => 此欄位是否需要排序
    ],
    stateSelect: [
      // 控制編輯文章檢舉下拉選單的 val & item
      { val: 0, item: "處理中" },
      { val: 1, item: "已審核" },
    ],
    reportSelect: [
      // 控制編輯文章檢舉下拉選單的 val & item
      { val: 0, item: "未審核" },
      { val: 1, item: "通過" },
      { val: 2, item: "未通過" },
    ],
    editedIndex: -1, //  控制目前點選編輯或是刪除該物件之index
    editedItem: {
      //  控制目前點選編輯或是刪除該筆物件之內容
      article_title: "文章標題",
      report_date: "檢舉時間",
      mem_name: "檢舉者",
      report_report: "處理措施",
      report_state: "檢舉處理狀態",
    },
    defaultItem: {
      //  初始值
      article_title: "文章標題",
      report_date: "檢舉時間",
      mem_name: "檢舉者",
      report_report: "處理措施",
      report_state: "檢舉處理狀態",
    },
    time: "", //  FOR 文章發布日期  => 但是有 Bug QQ
  }),
  mounted() {
    // 從PHP執行sql, 獲取表格所需的資料
    let params = new URLSearchParams();
    params.append("case", "article");
    this.axios
      .post("https://tibamef2e.com/cfd104/g4/back/api/reports.php", params)
      .then((res) => {
        this.tableData = res.data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
  computed: {},
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  created() {
    this.initialize();
  },
  methods: {
    initialize() {
      this.tableData = [
        {
          name: "美妝",
          account: "2022-02-27",
          access: "7pupu",
          status: "異常",
        },
        {
          name: "感情",
          account: "2022-02-26",
          access: "抹茶拿鐵",
          status: "正常",
        },
      ];
    },
    State: (v) => {
      // 因資料拿回的狀態是0/1 -> 所以將其轉換成相對應文字
      if (v == 0) {
        return "處理中";
      } else {
        return "已審核";
      }
    },
    report: (v) => {
      // 因資料拿回的狀態是0/1 -> 所以將其轉換成相對應文字
      if (v == 0) {
        return "未審核";
      } else if (v == 1) {
        return "通過";
      } else {
        return "未通過";
      }
    },

    getColor(state) {
      //用於控制 state 的 v-chip 顏色
      if (state == 1) return "default";
      else if (state == 0) return "primary";
    },

    reportColor(state) {
      //用於控制 state 的 v-chip 顏色
      if (state == 0) {
        return "default";
      } else if (state == 1) {
        return "primary";
      } else {
        return "secondary";
      }
    },

    editItem(item) {
      //編輯物件
      this.editedIndex = this.tableData.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      //刪除物件
      this.editedIndex = this.tableData.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      let params = new URLSearchParams();
      params.append("article_report_no", this.editedItem.article_report_no);
      params.append("phpState", "deleteArticle");
      this.axios
        .post("https://tibamef2e.com/cfd104/g4/back/api/editreport.php", params)
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
      this.tableData.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.dialogNew = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    save() {
      //for 修改
      if (this.editedIndex > -1) {
        Object.assign(this.tableData[this.editedIndex], this.editedItem);
        let params = new URLSearchParams();
        params.append("article_report_no", this.editedItem.article_report_no);
        params.append("report_report", this.editedItem.report_report);
        params.append("report_state", this.editedItem.report_state);
        params.append("phpState", "articleRenew");
        this.axios
          .post("https://tibamef2e.com/cfd104/g4/back/api/editreport.php", params)
          .then((res) => {
            console.log(res);
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        return false;
      }
      this.close();
    },
    createNew() {
      //for 新增
      let params = new URLSearchParams();
      params.append("billboard_name", this.editedItem.billboard_name);
      params.append("billboard_state", this.editedItem.billboard_state);
      params.append("mem_name", this.editedItem.mem_name);
      params.append("mem_no", this.editedItem.mem_no);
      params.append("phpState", "createNew");
      this.axios
        .post("https://tibamef2e.com/cfd104/g4/back/api/editbillboard.php", params)
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
      this.tableData.push(this.editedItem);
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
      this.close();
      this.reload();
    },
  },
};
</script>

<style scoped>
::v-deep .v-label {
  /* transform: translateY(-20px); */
  height: 30px;
}
.txt {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 36px 0 24px;
  margin: 10px 0;
}
.article_content {
  padding: 0 36px 0 24px;
  margin: 10px 0;
}
.selector {
  width: 150px;
}
.btn {
  display: flex;
  justify-content: space-evenly;
  padding-bottom: 20px;
}
.img {
  max-width: 150px;
  width: 150px;
  height: 150px;
  padding: 10px;
}
img {
  width: 100%;
}
</style>