<template>
  <div class="Boards-page">
    <!-- 頁面標題 & 新增按鈕 -->
    <v-row no-gutters class="d-flex justify-space-between mt-10 mb-6">
      <h1 class="page-title">CIMI | 會員管理</h1>
    </v-row>

    <!------------ 表格開始!------------>
    <v-data-table
      :headers="headers"
      :items="tableData"
      :search="search"
      sort-by="account"
      class="elevation-1"
    >
      <!-- 會員權限slot: 正常看板以及異常看板有不同顏色 -->
      <template v-slot:[`item.mem_power`]="{ item }">
        <v-chip class="ma-2" :color="powerColor(item.mem_power)" outlined pill>
          {{ memPower(item.mem_power) }}
          <v-icon right> mdi-account-outline </v-icon>
        </v-chip>
      </template>

      <!-- 會員帳號狀態slot: 正常看板以及異常看板有不同顏色 -->
      <template v-slot:[`item.mem_state`]="{ item }">
        <v-chip :color="getColor(item.mem_state)">
          {{ State(item.mem_state) }}
        </v-chip>
      </template>

      <!-- 編輯的SLOT -->
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
        <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
      </template>

      <!-- 頂部文章列表的SLOT -->
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>會員列表</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>

          <!-- 搜尋框 -->
          <v-text-field
            hide-details
            append-icon="mdi-magnify"
            single-line
            class="shrink pr-6"
            v-model="search"
            label="Search"
          ></v-text-field>

          <!-- 下載CSV -->
          <download-csv :data="tableData">
            <v-btn icon alt="下載CSV" title="">
              <v-icon class="mdi-36px pl-2 pr-2">
                mdi-arrow-down-bold-box
              </v-icon>
            </v-btn>
          </download-csv>

          <!-- 編輯dialog -->
          <v-dialog
            v-model="dialog"
            @save="save"
            @cancel="cancel"
            max-width="400"
          >
            <v-card class="mx-auto" max-width="400" tile>
              <v-card-title>
                <span class="text-h5">會員資訊</span>
              </v-card-title>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-account</v-icon>
                  <span class="text-subtitle-1">會員ID :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.mem_no }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-account</v-icon>
                  <span class="text-subtitle-1">會員帳號 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.mem_id }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-account</v-icon>
                  <span class="text-subtitle-1">會員名稱 :</span>
                </div>
                <div class="meminfo">
                  {{ editedItem.mem_name }}
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-account-edit</v-icon>
                  <span class="text-subtitle-1">會員權限 :</span>
                </div>
                <div class="meminfo">
                  <v-select
                    :items="powers"
                    item-text="item"
                    item-value="val"
                    v-model="editedItem.mem_power"
                    class="selector"
                  ></v-select>
                </div>
              </div>

              <div class="txt">
                <div class="title" col="12" sm="6">
                  <v-icon>mdi-account-edit</v-icon>
                  <span class="text-subtitle-1">會員狀態 :</span>
                </div>
                <div class="meminfo">
                  <v-select
                    :items="states"
                    item-text="item"
                    item-value="val"
                    v-model="editedItem.mem_state"
                    width="80px"
                    class="selector"
                  ></v-select>
                </div>
              </div>

              <v-spacer></v-spacer>

              <div class="btn">
                <v-btn color="blue darken-1" text @click="dialog = false"
                  >取消</v-btn
                >
                <v-btn color="blue darken-1" text @click="save(editedIndex)"
                  >確認</v-btn
                >
              </div>
            </v-card>
          </v-dialog>

          <!-- 刪除dialog -->
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">
                確認刪除此筆會員資訊 ?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  >取消</v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  >確認</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
    </v-data-table>
    <!----------- 表格結束!------------>
  </div>
</template>

<script>
export default {
  inject: ["reload"],
  data: () => ({
    search: "", // 搜尋列的v-model
    dialog: false, // 控制EDIT DIALOG 顯示或是不顯示
    dialogDelete: false, // 控制DELETE DIALOG 顯示或是不顯示
    dialogNew: false,
    tableData: [], // 表格的所有資料
    headers: [
      // 表格的欄位名稱
      {
        text: "會員ID",
        align: "start",
        value: "mem_no",
      },
      { text: "會員帳號", value: "mem_id" },
      { text: "會員名稱", value: "mem_name" },
      { text: "會員權限", value: "mem_power" },
      { text: "帳號狀態", value: "mem_state" },
      { text: "編輯", value: "actions", sortable: false }, // sortable => 此欄位是否需要排序
    ],
    powers: [
      { val: 0, item: "一般會員" },
      { val: 1, item: "看板版主" },
      { val: 2, item: "管理員" },
    ],
    states: [
      { val: 0, item: "正常" },
      { val: 1, item: "停權" },
    ],
    editedIndex: -1, //  控制目前點選編輯或是刪除該物件之index
    editedItem: {
      //  控制目前點選編輯或是刪除該筆物件之內容
      billboard_no: "美妝",
      publish_date: null,
      article_title: "請輸入文章標題",
      article_state: "0",
      article_content: "",
    },
    defaultItem: {
      //  初始值
      billboard_no: "選擇看板名稱",
      publish_date: null,
      article_title: "請輸入文章標題",
      article_state: "0",
      article_content: "",
    },
    // today:this.editDate,
  }),
  mounted() {
    //  從PHP執行sql, 獲取表格所需的資料
    this.axios
      .get("https://tibamef2e.com/cfd104/g4/back/api/mems.php")
      .then((res) => {
        console.log(res);
        this.tableData = res.data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
  computed: {
    formTitle() {
      // 控制新增或編輯之dialog => if true new item, else edit item
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  created() {
    this.initialize();
  },
  methods: {
    initialize() {
      this.tableData = [
        {
          name: "美妝",
          account: "2022-02-27",
          access: "7pupu",
          status: "異常",
        },
        {
          name: "感情",
          account: "2022-02-26",
          access: "抹茶拿鐵",
          status: "正常",
        },
      ];
    },
    State: (v) => {
      // 因資料拿回的狀態是0/1 -> 所以將其轉換成相對應文字
      if (v == 0) {
        return "正常";
      } else {
        return "停權";
      }
    },
    memPower: (v) => {
      if (v == "1") {
        return "看板版主";
      } else if (v == "2") {
        return "管理員";
      } else {
        return "一般會員";
      }
    },

    getColor(v) {
      //用於控制 state 的 v-chip 顏色
      if (v == 1) return "default";
      else if (v == 0) return "primary";
    },
    powerColor(v) {
      if (v == 0) {
        return "default";
      } else if (v == 1) {
        return "primary";
      } else {
        return "success";
      }
    },

    editItem(item) {
      //編輯物件
      this.editedIndex = this.tableData.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      //刪除物件
      this.editedIndex = this.tableData.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.dialogDelete = false;
      let params = new URLSearchParams();
      params.append("mem_no", this.editedItem.mem_no);
      params.append("state", "delete");
      this.axios
        .post("https://tibamef2e.com/cfd104/g4/back/api/editmem.php", params)
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
      this.tableData.splice(this.editedIndex, 1);
    },

    close() {
      this.dialog = false;
      this.dialogNew = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    save() {
      //for 修改
      Object.assign(this.tableData[this.editedIndex], this.editedItem);
      this.dialog = false;
      let params = new URLSearchParams();
      params.append("mem_no", this.editedItem.mem_no);
      params.append("mem_power", this.editedItem.mem_power);
      params.append("mem_state", this.editedItem.mem_state);
      params.append("state", "renew");
      this.axios
        .post("https://tibamef2e.com/cfd104/g4/back/api/editmem.php?case=2", params)
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

<style scoped>
.txt {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 36px 0 24px;
  margin: 10px 0;
}
.selector {
  width: 150px;
}
.btn {
  display: flex;
  justify-content: space-evenly;
  padding-bottom: 20px;
}
</style>